<template>
  <div id="login">
    <div class="waveWrapper waveAnimation">
      <div class="waveWrapperInner bgTop">
        <div class="wave waveTop"
             style=""></div>
      </div>
      <div class="waveWrapperInner bgMiddle">
        <div class="wave waveMiddle"
             style=""></div>
      </div>
      <div class="waveWrapperInner bgBottom">
        <div class="wave waveBottom"
             style=""></div>
      </div>

      <h2 class="title">WMS-仓库系统</h2>
    </div>
    <van-form @submit="onSubmit">
      <van-field class="login-field"
                 name='username'
                 v-model="user.username"
                 clearable
                 left-icon="user-o"
                 placeholder="请输入账号" />

      <van-field class="login-field"
                 name='password'
                 type="password"
                 v-model="user.password"
                 clearable
                 left-icon="closed-eye"
                 placeholder="请输入密码" />

      <div class="login-button">
        <van-button block
                    color="#f5a13f"
                    type="primary"
                    native-type="submit">
          提交
        </van-button>
      </div>
    </van-form>
  </div>
</template>
<script>
import { Notify } from 'vant';
let _this = this
export default {
  components: {
    [Notify.Component.name]: Notify.Component,
  },

  data () {
    return {
      user: {
        username: '',
        password: '',
      }

    }
  },
  mounted () {

  },
  methods: {
    onSubmit (values) {
      if (values.username == 'admin' && values.password == '123456') {
        this.$store.state.token = true
        this.$router.push({ path: '/' });
      } else {
        Notify({ type: 'danger', message: '请核对账号密码' });
      }
    },
  },
}
</script>

<style lang="stylus">
#login
  .van-field__left-icon
    padding-right 10px
    margin-right 10px
    position relative
    &:after
      content ''
      position absolute
      width 2px
      height 60%
      background-color #f5a13f
      right 0
      top 50%
      transform translateY(-50%)
    .van-icon
      font-size 18px
      color #f5a13f
  .van-form
    margin-top 30px
    .login-field
      margin-bottom 15px
      &:after
        border-color #f5a13f
        margin 0 15px
        left 0
  .login-button
    margin 30px 15px
@keyframes move_wave
  0%
    transform translateX(0) translateZ(0) scaleY(1)
  50%
    transform translateX(-25%) translateZ(0) scaleY(0.55)
  100%
    transform translateX(-50%) translateZ(0) scaleY(1)
.title
  position absolute
  z-index 111
  color #ffffff
  text-align center
  left 50%
  top 37%
  transform translate(-50%, -50%)
  font-weight 100
.waveWrapper
  overflow hidden
  position relative
  height 200px
  width 100%
.waveWrapperInner
  position absolute
  width 100%
  overflow hidden
  height 100%
  background-image linear-gradient(to top, #f5a13f 20%, #fe8407 80%)
.bgTop
  z-index 15
  opacity 0.5
.bgMiddle
  z-index 10
  opacity 0.75
.bgBottom
  z-index 5
.wave
  position absolute
  left 0
  width 200%
  height 100%
  background-repeat repeat no-repeat
  background-position 0 bottom
  transform-origin center bottom
.waveTop
  background-size 50% 30px
  background-image url('../../assets/img/wave-top.png')
.waveAnimation .waveTop
  animation move_wave 10s linear infinite
.waveMiddle
  background-size 50% 60px
  background-image url('../../assets/img/wave-mid.png')
.waveAnimation .waveMiddle
  animation move_wave 10s linear infinite
.waveBottom
  background-size 50% 40px
  background-image url('../../assets/img/wave-bot1.png')
.waveAnimation .waveBottom
  animation move_wave 15s linear infinite
</style>