<template>
  <div id="storageDetails">
    <navBar :title='title'
            :color="colorIf" />

    <van-form class="storageDetails-form"
              validate-first>

      <!-- 产品 -->

      <van-field readonly
                 clickable
                 label="产品"
                 name="name"
                 :value="formData.name[formData.name.length-1]"
                 input-align="right" />

      <!-- 单位 -->

      <van-field readonly
                 clickable
                 label="单位"
                 name="unit"
                 :value="formData.unit"
                 input-align="right" />

      <!-- 数量 -->

      <van-field v-model="formData.quantity"
                 name="quantity"
                 type="digit"
                 label="数量"
                 input-align="right" />

      <!-- 日期 -->
      <van-field readonly
                 clickable
                 name="calendar"
                 :value="formData.date"
                 label="日期"
                 input-align="right" />

      <!-- 管理员 -->
      <van-cell-group>
        <van-field v-model="formData.administrator"
                   name="administrator"
                   label="入库管理员"
                   input-align="right" />
      </van-cell-group>

      <div class="storageDetails-button">
        <van-button type="info"
                    round
                    size="small"
                    @click="onModification">修改</van-button>
        <van-button type="danger"
                    round
                    size="small"
                    @click="onDel">删除</van-button>
      </div>
    </van-form>

  </div>
</template>
<script>

import navBar from '@/components/navBar.vue'
import BScroll from 'better-scroll'
export default {
  components: {
    navBar
  },
  data () {
    return {
      title: '出库',
      colorIf: true,
      formData: {
        date: '2020-6-11',
        name: ['非标防冻液-2升'],
        administrator: '吴正新',
        quantity: '12',
        unit: '箱'
      }

    }
  }, mounted () {

  },
  methods: {
    onFailed (errorInfo) {
      this.$notify({ type: 'success', message: '提交成功' });
      this.$router.push({
        path: '/storage'
      })
    },
    onDel () {

      this.$dialog.confirm({
        title: '提示',
        message: '是否确定要删除此信息！',
      })
        .then(() => {
          this.$router.push({
            path: '/comeLibraryList'
          })
        })
        .catch(() => {
          // on cancel
        });

    },
    onModification () {
      this.$router.push({
        path: '/comeLibraryForm'
      })
    }


  }
}
</script>
<style lang="stylus">
.wrapper
  height 100vh
  overflow hidden
#storageDetails
  position relative
  overflow hidden
  &:after
    position absolute
    content ''
    width 600px
    height 600px
    background linear-gradient(to top left, #fe8407 20%, #f5a13f 100%)
    border-radius 50%
    transform translate(-38%, -72%)
    top 0
    z-index -1
    animation mymove 10s infinite
  &:before
    position absolute
    content ''
    width 300px
    height 300px
    border-radius 50%
    background-color #fcd2a8
    transform translate(80%, -50%)
    top 0
    z-index -1
    animation mymove2 10s infinite
  .storageDetails-form
    background-color #ffffff
    margin 0 15px
    border-radius 12px
    overflow hidden
    box-shadow 0 0 5px #b6b0aa
    .storageDetails-button
      display flex
      align-items center
      justify-content space-evenly
      margin 15px 0 15px
@keyframes mymove
  0%
    transform translate(-38%, -72%)
  50%
    transform translate(-45%, -70%)
  100%
    transform translate(-38%, -72%)
@keyframes mymove2
  0%
    transform translate(80%, -50%)
  50%
    transform translate(85%, -40%)
  100%
    transform translate(80%, -50%)
</style>