<template>
  <div id="inventory">
    <navBar :title='title'
            :color="colorIf" />
    <div id="main"
         style="width: 100%;height:200px;margin-bottom:15px"></div>

    <ul class="inventory-ul">
      <li class="inventory-li"
          v-for="(item,index) in inventory"
          :key="index">
        <div class="inventory-left">{{item.name}}</div>
        <div class="inventory-right">
          <span>{{item.quantity}}</span>
          <i>{{item.unit}}</i>
        </div>
      </li>
    </ul>

  </div>
</template>


<script>


import navBar from '@/components/navBar.vue'

export default {
  components: {
    navBar
  },
  data () {
    return {
      title: '库存',
      colorIf: false,
      inventory: [
        {
          name: '非标防冻液-1.5升',
          unit: '箱',
          quantity: '100'
        },
        {
          name: '非标防冻液-2升',
          unit: '箱',
          quantity: '40'
        },
        {
          name: '非标防冻液-4升',
          unit: '桶',
          quantity: '23'
        },
        {
          name: '非标防冻液-10升',
          unit: '桶',
          quantity: '20'
        },
        {
          name: '玻璃水-1.8升',
          unit: '箱',
          quantity: '60'
        }
      ]

    }
  },
  mounted () {
    this.drawChart()
  },
  methods: {
    drawChart () {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("main"));

      // 指定图表的配置项和数据
      let option = {
        backgroundColor: "#fe8407",
        title: {
          text: '仓库动态',
          textStyle: {
            align: 'center',
            color: '#fff',
            fontSize: 16,
          },
          top: '5%',
          left: 'center',
        },
        tooltip: {
        },
        grid: {
          top: '10%',
          left: '10%',
          right: '10%',
          bottom: '5%',
          containLabel: true,
        },
        xAxis: [{
          type: 'category',
          boundaryGap: false,
          axisLine: { //坐标轴轴线相关设置。数学上的x轴
            show: false,
            lineStyle: {
              color: '#ffffff'
            },
          },
          axisLabel: { //坐标轴刻度标签的相关设置
            textStyle: {
              color: '#ffffff',
              margin: 10,
            },
          },
          axisTick: { show: false, },
          data: ['6.11', '6.12', '6.13', '6.14', '6.15'],
        }],
        yAxis: [{
          type: 'value',
          splitNumber: 3,
          splitLine: {
            show: false,
            lineStyle: {
              color: '#ffffff'
            }
          },
          axisLine: { show: false, },
          axisLabel: {
            show: false,
            margin: 20,
            textStyle: {
              color: '#ffffff',

            },
          },
          axisTick: { show: false, },
        }],
        series: [{
          name: '异常流量',
          type: 'line',
          smooth: true, //是否平滑曲线显示
          // 			symbol:'circle',  // 默认是空心圆（中间是白色的），改成实心圆
          symbolSize: 0,

          lineStyle: {
            normal: {
              color: "#ffffff"   // 线条颜色
            }
          },

          data: [90, 105, 84, 125, 110]
        }]
      };


      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option);
    }

  }
}
</script>

<style lang="stylus">
#inventory
  .inventory-ul
    .inventory-li
      display flex
      height 60px
      margin 0 15px 15px
      justify-content space-between
      align-items center
      padding 0 15px
      border-radius 4px
      box-shadow 0 0 15px #c5c5c5
      &:nth-child(1)
        background-color #f44336
      &:nth-child(2)
        background-color #ff9800
      &:nth-child(3)
        background-color #ffeb3b
      &:nth-child(4)
        background-color #8bc34a
      &:nth-child(5)
        background-color #9c27b0
      .inventory-left
        font-size 16px
        color #ffffff
      .inventory-right
        align-items baseline
        display flex
        span
          display inline-block
          margin-right 5px
          font-size 16px
          color #ffffff
        i
          font-size 12px
          color #ffffff
          font-style normal
</style>

