<template>
  <div id="settin">

    <navBar :title='title'
            :color="colorIf" />

    <div class="settin-form">
      <van-cell-group>
        <van-field v-model="settinData.name"
                   label="账号"
                   readonly />
      </van-cell-group>
      <van-cell-group>
        <van-field v-model="settinData.time"
                   readonly
                   label="登录时间" />
      </van-cell-group>
      <van-cell-group>
        <van-field label="消息通知"
                   readonly
                   right-icon="arrow" />
      </van-cell-group>
    </div>
    <div class="settin-button">
      <van-button color="#f5a13f"
                  type="primary"
                  @click="goQuit"
                  block>退出</van-button>
    </div>

  </div>
</template>

<script>

import navBar from '@/components/navBar.vue'
import { Dialog } from 'vant';
export default {
  components: {
    navBar,
    [Dialog.Component.name]: Dialog.Component,


  },

  data () {
    return {
      title: '设置',
      colorIf: true,
      settinData: {
        name: '超级管理员',
        time: '2020-6-10'
      }
    }
  },
  mounted () {

  },
  methods: {
    goQuit () {
      Dialog.confirm({
        title: '提示',
        message: '是否确定要退出!',
      })
        .then(() => {
          this.$router.push({ path: '/login' });
        })
        .catch(() => {
          // on cancel
        });
    }
  }
}
</script>

<style lang="stylus">
#settin
  position relative
  overflow hidden
  &:after
    position absolute
    content ''
    width 600px
    height 600px
    background linear-gradient(to top left, #fe8407 20%, #f5a13f 100%)
    border-radius 50%
    transform translate(-38%, -72%)
    top 0
    z-index -1
    animation mymove 10s infinite
  &:before
    position absolute
    content ''
    width 300px
    height 300px
    border-radius 50%
    background-color #fcd2a8
    transform translate(80%, -50%)
    top 0
    z-index -1
    animation mymove2 10s infinite
  .settin-form
    margin 0 15px 0
    box-shadow 0 0 5px #f1e8df
  .settin-button
    margin 50px 15px 0
@keyframes mymove
  0%
    transform translate(-38%, -72%)
  50%
    transform translate(-45%, -70%)
  100%
    transform translate(-38%, -72%)
@keyframes mymove2
  0%
    transform translate(80%, -50%)
  50%
    transform translate(85%, -40%)
  100%
    transform translate(80%, -50%)
</style>