<template>
  <div id="storageList">
    <navBar :title='title'
            :color="colorIf" />

    <!-- <van-pull-refresh class="storageForm-content"
                      v-model="isLoading"
                      @refresh="onRefresh"> -->
    <div class="storageForm-content">

      <div class="wrapper"
           ref="wrapper">
        <div class="content">

          <div class="storageForm-list"
               v-for="(item,index) in storageList"
               :key="index">
            <div class="storageForm-list-title">
              <van-icon name="edit" />
              <h2 class="storageForm-list-title-h2">商品名称:{{item.name}}</h2>
            </div>
            <van-cell title="数量"
                      value="18"
                      :border="false" />
            <van-cell title="日期"
                      value="2020-6-11"
                      :border="false" />
            <van-cell title="入库管理员"
                      value="吴正新"
                      :border="false" />
            <div class="storageForm-list-button">
              <van-button round
                          size="small"
                          color="#fe8407"
                          @click="goStorageDetails"
                          type="info">查看详情</van-button>
            </div>

          </div>

        </div>
      </div>
    </div>
    <!-- </van-pull-refresh> -->

  </div>
</template>
<script>


import navBar from '@/components/navBar.vue'
import Bscroll from 'better-scroll'
import { Dialog } from 'vant';
export default {
  components: {
    navBar,
    [Dialog.Component.name]: Dialog.Component,
  },
  data () {
    return {
      title: '入库列表',
      colorIf: true,
      isLoading: false,
      storageList: [{ name: '防冻液', date: '2020-6-11' }, { name: '玻璃水', date: '2020-6-11' }, { name: '玻璃水', date: '2020-6-11' }]

    }
  },
  mounted () {
    this.$nextTick(() => {
      this.scroll = new Bscroll(this.$refs.wrapper, {})
    })
  },
  methods: {
    //   下拉事件
    // onRefresh () {
    //   setTimeout(() => {
    //     this.isLoading = false;
    //   }, 1000);
    // },
    goStorageDetails () {
      this.$router.push({
        path: '/storageDetails'
      })
    }

  }
}
</script>
<style lang="stylus">
#storageList
  position relative
  overflow hidden
  .storageForm-content
    height calc(100vh - 46px)
    position relative
    .storageForm-list
      background-color #ffffff
      margin 0 15px 20px
      padding 15px
      border-radius 12px
      box-shadow 0 0 5px #b6b0aa
      overflow hidden
      .van-icon
        margin-right 10px
        font-size 14px
        color #fe8407
      .storageForm-list-title
        display flex
        align-items center
        border-bottom 1px solid #f3f3f3
        padding-bottom 10px
        .storageForm-list-title-h2
          font-size 16px
          margin 0
      .van-cell
        padding 5px 0px
      .storageForm-list-button
        text-align center
        .van-button--small
          min-width 100px
  .wrapper
    position absolute
    left 0
    top 0
    right 0
    bottom 0
  &:after
    position absolute
    content ''
    width 600px
    height 600px
    background linear-gradient(to top left, #fe8407 20%, #f5a13f 100%)
    border-radius 50%
    transform translate(-38%, -72%)
    top 0
    z-index -1
    animation mymove 10s infinite
  &:before
    position absolute
    content ''
    width 300px
    height 300px
    border-radius 50%
    background-color #fcd2a8
    transform translate(80%, -50%)
    top 0
    z-index -1
    animation mymove2 10s infinite
  .settin-form
    margin 0 15px 0
    box-shadow 0 0 5px #f1e8df
  .settin-button
    margin 50px 15px 0
@keyframes mymove
  0%
    transform translate(-38%, -72%)
  50%
    transform translate(-45%, -70%)
  100%
    transform translate(-38%, -72%)
@keyframes mymove2
  0%
    transform translate(80%, -50%)
  50%
    transform translate(85%, -40%)
  100%
    transform translate(80%, -50%)
</style>